/* GLOBAL CHANGES AND OVERRIDES*/

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  height: 100vh;
}

body {
  margin: -20px 0 0 0;
}

.App {
  text-align: center;
}

.App-header,
.card-manager-wrapper {
  background-color: #009150;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 10px;
  font-weight: 100;
  color: white;
  text-decoration: underline;
  -webkit-text-decoration-color: #ff8c00;
          text-decoration-color: #ff8c00;
}

.card-manager-wrapper {
  height: 9%;
  font-size: 2em;
  margin: 11px 0;
}

.App-header {
  height: 15%;
  font-size: calc(50vw * .1);
}

.App-header h2 {
 position: relative;
 top: 8px; 
}

.App-intro {
  font-size: large;
}

.pageContainer {
  height: 100vh;
}

.categorySelector {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: auto;
  max-width: 1200px;
}

.categorySelectorBlock {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 20px;
  font-size: 27px;
}

.categorySelectorBlock:hover {
  cursor: pointer;
  -webkit-animation: wobble .2s ease !important;
          animation: wobble .2s ease !important;
}

.categorySelectorModal {
  /* width: 100%; */
  text-align: center;
}

.categorySelectorModal h3 {
  padding: 0 10px !important;
}

.modalBox {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.Select-menu-outer {
  width: 80%;
  left: 50%;
  -webkit-transform: translatex(-50%);
      -ms-transform: translatex(-50%);
          transform: translatex(-50%);
  position: relative; 
}

.modalBoxPaper {
  -ms-flex: 1 1;
      flex: 1 1;
  width: 32%;
  padding: 5px 0;
  margin: 5px;
  cursor: pointer;
}

.card-form-input::-webkit-input-placeholder {
  color: #AAAAAA;
}

.card-form-input:-ms-input-placeholder {
  color: #AAAAAA;
}

.card-form-input::placeholder {
  color: #AAAAAA;
}

#card-form-selector::options {

}

#card-form-wrapper button:hover {
  opacity: .9;
  cursor: pointer;
}

#footer-spacer {
  height: 50px;
}

/* .categorySelectorBlock h4 {
  text-align: center;
  font-size: 20px;
} */

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes wobble {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(5deg);
    transform: rotate(5deg);
  }
  50% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  75% {
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

@keyframes wobble {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(5deg);
    transform: rotate(5deg);
  }
  50% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  75% {
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

@media (min-width: 800px){
  .App-header {
    font-size: 2.5em;
  }
}



